header {
  margin-bottom: 7rem; }
  header .logo {
    margin-right: 20px; }
  header ul li .nav-link {
    color: white; }
    header ul li .nav-link:hover {
      color: #6c757d !important; }

footer {
  margin-top: 6rem; }

.modal-body ul {
  list-style: none; }

.modal-body li {
  font-size: 1.2rem;
  margin-bottom: 10px; }

.tabulator {
  background-color: transparent;
  border: unset;
  border-radius: 8px; }

.tabulator .tabulator-header {
  border-bottom: unset;
  border-left: 1px solid #999;
  border-right: 1px solid #999; }
  .tabulator .tabulator-header .tabulator-col {
    background-color: #212529;
    color: #fff;
    padding: 15px;
    height: auto !important;
    text-transform: uppercase;
    text-align: center; }
    .tabulator .tabulator-header .tabulator-col:hover {
      background-color: rgba(33, 37, 41, 0.9) !important; }

.table > :not(caption) > * > * {
  padding: 0;
  display: flex; }

.tabulator .tabulator-tableholder {
  white-space: nowrap;
  overflow: hidden;
  border-top: unset; }
  .tabulator .tabulator-tableholder .tabulator-table {
    border: 1px solid #999;
    width: 100%;
    border-radius: 0 0 8px 8px; }

.tabulator .tabulator-header .tabulator-col.tabulator-sortable[aria-sort=desc] .tabulator-col-content .tabulator-col-sorter .tabulator-arrow {
  border-top: 6px solid #fff; }

.tabulator .tabulator-header .tabulator-col.tabulator-sortable[aria-sort=asc] .tabulator-col-content .tabulator-col-sorter .tabulator-arrow {
  border-bottom: 6px solid #fff; }

[aria-label="First Page"],
[aria-label="Prev Page"],
[aria-label="Next Page"],
[aria-label="Last Page"] {
  display: none !important; }

.tabulator-row .tabulator-cell {
  font-size: 1rem;
  padding: 15px;
  font-weight: 600;
  transition: all .15s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center; }
  .tabulator-row .tabulator-cell:not(:last-of-type):hover {
    background-color: #3DA5D9;
    color: white; }

.modal-body li {
  color: #3DA5D9;
  font-size: 1.2rem;
  display: flex;
  justify-content: space-between;
  width: 70%; }
  .modal-body li span {
    margin-right: auto;
    color: black;
    font-size: 18px; }
  .modal-body li small {
    min-width: 9rem;
    text-align: center; }

.table > :not(:first-child),
.tabulator .tabulator-footer {
  border-top: 0; }

.tabulator .tabulator-footer {
  display: flex;
  background-color: transparent;
  margin-top: 10px;
  margin-left: -16px; }

.tabulator .tabulator-footer .tabulator-page {
  color: black;
  padding: 8px 16px;
  text-decoration: none; }
  .tabulator .tabulator-footer .tabulator-page.active {
    background-color: #3DA5D9;
    color: white; }
  .tabulator .tabulator-footer .tabulator-page.active:hover {
    background-color: #0d6efd !important;
    color: white !important; }
  .tabulator .tabulator-footer .tabulator-page:hover:not(.active) {
    background-color: #ddd !important;
    color: black !important; }

.tabulator-row.tabulator-selectable:hover {
  background-color: transparent;
  cursor: unset; }

.tabulator-row.tabulator-row-even:hover {
  background-color: #efefef; }

.footer-section {
  background: #212529;
  position: relative; }

.footer-cta {
  border-bottom: 1px solid #373636; }

.single-cta i {
  color: #FFC107;
  font-size: 30px;
  float: left;
  margin-top: 8px; }

.cta-text {
  padding-left: 15px;
  display: inline-block; }

.cta-text h4 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 2px; }

.cta-text span {
  color: #757575;
  font-size: 15px; }

.footer-content {
  position: relative;
  z-index: 2; }

.footer-pattern img {
  position: absolute;
  top: 0;
  left: 0;
  height: 330px;
  background-size: cover;
  background-position: 100% 100%; }

.footer-logo {
  margin-bottom: 30px; }

.footer-logo img {
  max-width: 135px; }

.footer-text p {
  margin-bottom: 14px;
  font-size: 14px;
  color: #7e7e7e;
  line-height: 28px; }

.footer-social-icon span {
  color: #fff;
  display: block;
  font-size: 20px;
  font-weight: 700;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 20px; }

.footer-social-icon a {
  color: #fff;
  font-size: 16px;
  margin-right: 15px; }

.footer-social-icon i {
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 38px;
  border-radius: 50%; }

.facebook-bg {
  background: #3B5998; }

.twitter-bg {
  background: #55ACEE; }

.google-bg {
  background: #DD4B39; }

.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative; }

.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
  background: #FFC107; }

.footer-widget ul li {
  display: inline-block;
  float: left;
  width: 50%;
  margin-bottom: 12px; }

.footer-widget ul li a:hover {
  color: #FFC107; }

.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize; }

.subscribe-form {
  position: relative;
  overflow: hidden; }

.subscribe-form input {
  width: 100%;
  padding: 14px 28px;
  background: #2E2E2E;
  border: 1px solid #2E2E2E;
  color: #fff; }

.subscribe-form button {
  position: absolute;
  right: 0;
  background: #FFC107;
  padding: 13px 20px;
  border: 1px solid #FFC107;
  top: 0; }

.subscribe-form button i {
  color: #fff;
  font-size: 22px;
  transform: rotate(-6deg); }

.copyright-area {
  background: #202020;
  padding: 25px 0; }

.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787; }

.copyright-text p a {
  color: #FFC107; }

.footer-menu li {
  display: inline-block;
  margin-left: 20px; }

.footer-menu li:hover a {
  color: #FFC107; }

.footer-menu li a {
  font-size: 14px;
  color: #878787; }

@media screen and (max-width: 992px) {
  .col-xl-4.col-lg-4.col-md-6.mb-30 {
    margin: 2rem 0; }
  .footer-cta {
    padding-bottom: 2rem !important; }
    .footer-cta .single-cta {
      margin-bottom: 1rem; }
  .footer-content {
    padding-top: 2rem !important; } }
